import React from 'react'
// import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'


// border border-primary rounded " style={{background:"lightblue"}}
// border border-success rounded"  style={{background:"lightblue"}}

export default function Header(props) {
  return (
    <div className="sticky-top">
    <nav className="navbar navbar-expand-lg border border-success" style={{background:"lightblue"}}>
        <div className="container-fluid ">
          <Link className="navbar-brand p-2 " to="/">{props.heading}</Link>
          <div className="collapse navbar-collapse " id="navbarSupportedContent">
            <ul className="d-flex navbar-nav me-auto mb-2  p-1 mb-lg-0 " >
              <li className="nav-item">
                <Link className="nav-link " style={{color:'orangered'}} to="/">Home</Link>
              </li> 
            
              <li className="nav-item">
                <Link className="nav-link " style={{color:'orangered'}} to="/">Pages</Link>
              </li>
              <li className="nav-item ">
                <Link className="nav-link " style={{color:'orangered'}} to="/">Blog</Link>
              </li>
              <li className="nav-item ">
                <Link className="nav-link " style={{color:'orangered'}} to="/contact">Contact us</Link>
              </li>
              <li className="nav-item ">
                <Link className="nav-link " style={{color:'orangered'}} to="/about">About</Link>
              </li>
            </ul>
   
             <form className="d-flex" role="search">
              <input className="form-control me-2 border border-success" type="search" placeholder="Search" aria-label="Search" />
              <button className="btn btn-outline-secondary bg-success text-white" type="submit">Search</button>
            </form>
          </div>
        </div>
      </nav>

    </div>
  )
}


