import React from 'react'
import { SocialIcon } from 'react-social-icons';


export const Contact = () => {
  let Footerstyle = {
    position: "absolute",
    top: "0vh",
    width: "100%"
  }
  return (
    <div className=" m-0 bg-dark border rounded" style={Footerstyle}>

      <div className="p-5 my-5 text-light ">
        <div className="p-2 ">
          <SocialIcon url="https://linkedin.com/in/raju-singh-sindal-a6348b1aa" />&emsp;Linked In
        </div>
        <div className="p-2">
          <SocialIcon url="https://instagram.com/rathorerajusingh786?igshid=hj84agdvqnb8" />&emsp;Instagram
        </div>
        <div className="p-2">
          <SocialIcon url="https://m.facebook.com/rajusingh.singhrathore" />&emsp;Facebook
        </div>
        <div className="p-2 pb-5">
          <SocialIcon url="https://twitter.com/RajuSin43350124?s=08" />&emsp;Twitter
        </div>
      </div>
    </div>

  )
}
